.product-detail {
	margin-top: 70px;

	.overview {

		.slider-ProductDetail {
			display: flex;

			.big-image {
				flex        : 0 0 600px;
				max-width   : 600px;
				margin-right: 35px;

				figure {
					height: 100%;
				}

				.swiper-container {
					height: 640px;
				}
			}

			.small-image {
				position : relative;
				flex     : 0 0 110px;
				max-width: 110px;

				figure {
					cursor: pointer;
					height: 110px;
				}

				.swiper-container {
					height: 375px;
				}

				.swiper-slide {
					height : 110px !important;
					opacity: .5;
				}

				.swiper-button-next,
				.swiper-button-prev {
					z-index          : 1;
					transition       : .3s all ease-in-out;
					background-repeat: no-repeat;
					background-size  : cover;
					top              : unset;
					bottom           : 0px;
					width            : 14px;
					height           : 8px;
				}

				.swiper-button-next {
					right           : 25px;
					background-image: url(../img/icons/next-slider-product-detail.svg);
				}

				.swiper-button-prev {
					left            : 25px;
					background-image: url(../img/icons/prev-slider-product-detail.svg);
				}

				.swiper-slide-thumb-active {
					opacity: 1;
				}


			}
		}

		.info-product {
			h1 {
				font-size    : 42px;
				font-weight  : 700;
				color        : $main-color-1;
				margin-bottom: 20px;
			}

			h5 {
				font-size     : 21px;
				color         : #777777;
				font-weight   : 400;
				padding-bottom: 25px;
				border-bottom : 1px solid #F5F5F5;
			}

			.desc {
				margin-top: 30px;

				p {
					font-size    : 17px;
					color        : #777777;
					margin-bottom: 20px;

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			.btn {
				position   : relative;
				margin-top : 100px;
				width      : 200px;
				line-height: 50px;

				p {
					display    : inline-block;
					margin-left: 10px;
				}
			}
		}
	}

	.tab-information {
		margin-top: 85px;

		.list-tab {
			display        : flex;
			flex-wrap      : wrap;
			justify-content: space-between;
			align-items    : center;
			border         : 1px solid #F5F5F5;

			.item {
				cursor     : pointer;
				user-select: none;
				position   : relative;
				flex       : 0 0 50%;
				max-width  : 50%;

				color      : $main-color-1;
				line-height: 75px;
				font-size  : 17px;
				font-weight: 700;
				text-align : center;
				transition : .3s all ease-in-out;

				&:after {
					content     : '';
					position    : absolute;
					left        : 50%;
					transform   : translateX(-50%);
					bottom      : -16px;
					border-left : 8px solid transparent;
					border-right: 8px solid transparent;
					border-top  : 16px solid $main-color-1;

					opacity   : 0;
					transition: .3s all ease-in-out;
				}

				&.active {
					color     : #FFFFFF;
					background: $main-color-1;
					box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: .16);

					&:after {
						opacity: 1;
					}
				}
			}
		}

		.tab-content {
			background: #FCFCFC;
			padding   : 60px;

			.content[tab-id="item-2"] {
				overflow-x: auto;
			}

			// .item {
			// 	margin-bottom: 70px;

			// 	h5 {
			// 		font-size    : 17px;
			// 		color        : #000000;
			// 		font-weight  : 700;
			// 		margin-bottom: 35px;
			// 	}

			// 	p {
			// 		font-size: 17px;
			// 		color    : #777777;
			// 	}
			// }

			// table {
			// 	width     : 100%;
			// 	text-align: center;

			// 	thead {
			// 		background: $main-color-1;
			// 	}

			// 	th,
			// 	td {
			// 		border   : 1px solid #707070;
			// 		padding  : 20px;
			// 		font-size: 18px;
			// 	}

			// 	th {
			// 		color: #ffffff;
			// 	}

			// 	td {
			// 		color: #434343;
			// 	}
			// }
		}
	}

	@media (max-width: 1600px) {
		padding   : 50px 0;
		margin-top: 0;

		.overview {

			.slider-ProductDetail {
				margin: 0 -10px;

				.big-image {
					flex        : 0 0 80%;
					max-width   : 80%;
					margin-right: 0;
					padding     : 0 10px;

					.swiper-container {
						height: 450px;
					}
				}

				.small-image {
					flex     : 0 0 20%;
					max-width: 20%;
					padding  : 0 10px;
				}
			}

			.info-product {
				margin-top: 10px;

				h1 {
					font-size    : 32px;
					margin-bottom: 10px;
				}

				h5 {
					padding-bottom: 10px;
				}

				.desc {
					margin-top: 15px;

					p {
						font-size: 16px;
					}
				}

				.btn {
					width    : 160px;
					font-size: 14px;
					margin   : 25px 0 0 0;
				}
			}
		}

		.tab-information {
			margin-top: 50px;

			.list-tab {
				.item {
					line-height: 50px;

					&:after {
						bottom    : -10px;
						border-top: 11px solid $main-color-1;
					}
				}
			}

			.tab-content {
				padding: 25px;

				.item {
					margin-bottom: 50px;

					h5 {
						margin-bottom: 15px;
					}

					&:last-child,
					&:nth-last-child(2) {
						margin-bottom: 0;
					}
				}
			}
		}
	}

	@media (max-width: 767.98px) {
		padding: 25px 0 0 0;

		.overview {
			.slider-ProductDetail {
				flex-direction: column;
				margin        : 0;

				.big-image {
					flex        : 0 0 100%;
					max-width   : 100%;
					margin-right: 0;
					padding     : 0;

					.swiper-container {
						height: 345px;
					}
				}

				.small-image {
					flex      : 0 0 100%;
					max-width : 100%;
					padding   : 0;
					margin-top: 10px;

					.swiper-container {
						height: auto;
					}

					.swiper-button-next,
					.swiper-button-prev {
						display: none;
					}

					figure {
						height: 108.33px;
					}
				}
			}

			.info-product {
				h1 {
					font-size: 18px;
				}

				h5 {
					font-size: 14px;
				}

				.desc {
					p {
						font-size    : 14px;
						margin-bottom: 5px;
					}
				}

				.btn {
					margin-top : 10px;
					line-height: 40px;
					font-size  : 12px;
					width      : 135px;

					p {
						font-weight: 400;
					}

					img {
						height: 15px;
					}
				}
			}
		}

		.tab-information {
			margin-top: 25px;

			.list-tab {
				.item {
					font-size  : 10px;
					line-height: 40px;
				}
			}

			.tab-content {
				padding: 15px;

				.item {
					margin-bottom: 15px;

					h5 {
						font-size: 16px;
					}

					p {
						font-size: 14px;
					}
				}

				table {
					width     : 100%;
					text-align: center;

					th,
					td {
						padding  : 10px;
						font-size: 14px;
					}

					th {
						color: $main-color-2;
					}

					td {
						color: #434343;
					}
				}
			}
		}
	}
}